<template>
  <div>
    <header class="mui-bar mui-bar-nav">
      <h1 class="mui-title">用户注册</h1>
    </header>
    <div class="mui-content" style="margin-top:3px;">
        <form class="mui-input-group">
          <div class="mui-input-row">
              <label>证件类型</label>
              <input type="text" class="mui-input-clear" placeholder="请选择证件类型" v-model="userType" @click="showIdType" readonly/>
          </div>
          <div class="mui-input-row">
              <label>证件号码</label>
              <input type="text" class="mui-input-clear" placeholder="请输入证件号码" v-model="userLoginCode"/>
          </div>
          <div class="mui-input-row">
              <label>手机号码</label>
              <input type="text" id="tel" class="mui-input-clear" placeholder="请输入手机号" v-model="userPhones"/>
              <!-- <button type="button" id="vercode" class="mui-btn mui-btn-primary mui-btn-outlined" @click="getVerCode">发送验证码</button> -->
          </div>
          
          <!-- <div class="mui-input-row">
            <label>验证码</label>
            <input type="text" class="mui-input-clear" placeholder="请输入验证码"/>
          </div> -->
          <div class="mui-button-row">
              <button type="button" data-loading-icon="mui-spinner mui-spinner-custom" 
              class="mui-btn mui-btn-primary" @click="UserLogin">注册</button>
          </div>

          <div id="certf_type" class="mui-popover mui-popover-bottom mui-popover-action">
            <ul class="mui-table-view">
              <li class="mui-table-view-cell"  v-for="item in selectData">
                <a href="javascript:return false;" @click="selectLi(item.parameterValue,item.areaCode)" >{{item.parameterValue}}</a>
              </li> 
            </ul>
            <ul class="mui-table-view">
              <li class="mui-table-view-cell">
                <a href="#certf_type"><b>取消</b></a>
              </li>
            </ul>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import axios from "axios";
    export default {
      name: "UserLogin",
      data () {
        return {
          userType : "居民身份证",
          userTypeCode : '120001',
          userLoginCode:"",
          userPhones:"",
          selectData:this.$store.getters.user.idTypeList,
          userCode: this.$store.getters.user.userCode
        }
      },
      methods : {
        selectLi: function(idRem,idType) {
          this.userType = idRem;
          this.userTypeCode = idType;
          this.mui("#certf_type").popover('toggle');
        },
        showIdType: function() {
          this.mui("#certf_type").popover('toggle');
        },
        UserLogin:function() {
          let userCode = this.userCode
          if(userCode){
            this.$store.commit('LocalUser',{userCode:userCode,userTypeCode:this.userTypeCode,userLoginCode:this.userLoginCode,userPhones:this.userPhones})
            console.log("用户标识："+userCode+" ;证件类型：" + this.userTypeCode + "_" + this.userType + 
                        " ;用户证件号码：" + this.userLoginCode + " ;用户手机号：" +  this.userPhones + 
                        " ；验证码:" + this.codeMsg)
            this.$http.post("/userRegister",{
                userCode:userCode,
                userTypeCode:this.userTypeCode,
                userLoginCode:this.userLoginCode,
                userPhones:this.userPhones
              })
              .then(
                (response)=>{
                  let type = response.data.status;
                  console.log(type)
                  if(type){
                    console.log( response.data.msg)
                    if(type=='identifying'){
                      this.$router.push('/identifying');
                    } else {
                      alert(response.data.msg)
                    }
                  }
                },
                (error)=>{
                  console.log(error);
                }
              )
          } else {
            alert("数据异常，请重新登录")
          }
        }
      }
    }
</script>
<style scoped>  
/* #tel{
  width:40%;
  float: left;
}
#vercode{
  float: left;
  width: 25%; */
  /* margin-top: 7.3px; */
  /* border: none;
} */
</style>


